	module.exports = {"data":{"markdownRemark":{"html":"<p>Common responses provide consistent messaging across the platform and prevent you from having to write out the same message again and again. When you select an action for a report, you can have a common response pre-populate for that specific action.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/common-response-1-ced4e441b06b9700e682acbe9f55a2c6-0b548.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 52%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"common-response-1\"\n        title=\"\"\n        src=\"/static/common-response-1-ced4e441b06b9700e682acbe9f55a2c6-2f183.png\"\n        srcset=\"/static/common-response-1-ced4e441b06b9700e682acbe9f55a2c6-67829.png 125w,\n/static/common-response-1-ced4e441b06b9700e682acbe9f55a2c6-5c59f.png 250w,\n/static/common-response-1-ced4e441b06b9700e682acbe9f55a2c6-2f183.png 500w,\n/static/common-response-1-ced4e441b06b9700e682acbe9f55a2c6-c93e8.png 750w,\n/static/common-response-1-ced4e441b06b9700e682acbe9f55a2c6-4e628.png 1000w,\n/static/common-response-1-ced4e441b06b9700e682acbe9f55a2c6-b3ac4.png 1500w,\n/static/common-response-1-ced4e441b06b9700e682acbe9f55a2c6-0b548.png 2400w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Set Up a Default Common Response</h3>\n<p>To set a default common response:</p>\n<ol>\n<li>Go to <strong>Settings > Program > Automation > Common Responses</strong>.</li>\n<li>You'll find a pre-populated list of responses that HackerOne finds useful.</li>\n<li>Click the <strong>Set default Common Responses</strong> button.</li>\n<li>In the <strong>Default Common Responses</strong> window, select the default response message from the drop-down next to the action.</li>\n<li>Click <strong>Save</strong>.</li>\n</ol>\n<p>The default common response will automatically populate when you select the action for the report.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/common-response-2-b41177b2146cb7c7403ec0a6ac320c4d-b3cba.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 493px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 142.59634888438134%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"common-response-2\"\n        title=\"\"\n        src=\"/static/common-response-2-b41177b2146cb7c7403ec0a6ac320c4d-b3cba.png\"\n        srcset=\"/static/common-response-2-b41177b2146cb7c7403ec0a6ac320c4d-1f5ec.png 125w,\n/static/common-response-2-b41177b2146cb7c7403ec0a6ac320c4d-c025f.png 250w,\n/static/common-response-2-b41177b2146cb7c7403ec0a6ac320c4d-b3cba.png 493w\"\n        sizes=\"(max-width: 493px) 100vw, 493px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Add Your Own Common Response</h3>\n<p>You can write your own common response\nTo add a new common response:</p>\n<ol>\n<li>Go to <strong>Settings > Program > Automation > Common Responses</strong>.</li>\n<li>Select the <strong>Add Common Response</strong> button.</li>\n<li>Type the title of your response in the <strong>Common Response Title</strong> field. This title will show up in the list of common responses to choose from.</li>\n<li>Type the message of your response in the <strong>Message</strong> field.</li>\n<li>Click <strong>Create Common Response</strong>.</li>\n</ol>\n<p>Your common response will display in the list of common responses to choose from when you set up a default common response.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/common-response-3-5e672cd6b30d2969cfd35acd58c54372-b9fe5.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 72.67759562841529%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"common-response-3\"\n        title=\"\"\n        src=\"/static/common-response-3-5e672cd6b30d2969cfd35acd58c54372-2f183.png\"\n        srcset=\"/static/common-response-3-5e672cd6b30d2969cfd35acd58c54372-67829.png 125w,\n/static/common-response-3-5e672cd6b30d2969cfd35acd58c54372-5c59f.png 250w,\n/static/common-response-3-5e672cd6b30d2969cfd35acd58c54372-2f183.png 500w,\n/static/common-response-3-5e672cd6b30d2969cfd35acd58c54372-c93e8.png 750w,\n/static/common-response-3-5e672cd6b30d2969cfd35acd58c54372-4e628.png 1000w,\n/static/common-response-3-5e672cd6b30d2969cfd35acd58c54372-b9fe5.png 1464w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Edit a Common Response</h3>\n<p>To edit a common response:</p>\n<ol>\n<li>Go to <strong>Settings > Program > Automation > Common Responses</strong>.</li>\n<li>Click the title of the response you want to edit.</li>\n<li>Edit the title and the message.</li>\n<li>Click <strong>Update Common Response</strong>.</li>\n</ol>","frontmatter":{"path":"/programs/common-responses.html","id":"programs/common-responses","title":"Common Responses","description":null}}},"pathContext":{}}

